import React, { Component, useState, useEffect } from "react"
import Layout from "../layout/Layout"
import { Container } from "@material-ui/core"
import { makeStyles } from '@material-ui/core/styles';
import OutlinedInput from '@material-ui/core/OutlinedInput';
import { Input } from '@material-ui/core';
import InputLabel from '@material-ui/core/InputLabel';
import InputAdornment from '@material-ui/core/InputAdornment';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles((theme) => ({
    margin: {
      margin: theme.spacing(1),
    }
  }));

export default () => {
    const classes = useStyles();

    const [litrosCloroDeseado, setLitrosCloroDeseado] = useState([])
    const [porcentajeCloroDeseado, setPorcentajeCloroDeseado] = useState(0.1)
    const [porcentajeCloroDispuesto, setPorcentajeCloroDispuesto] = useState(5)
    const [litrosCloro5, setLitrosCloro5] = useState(0)
    const [agua, setAgua] = useState(0)

    const calcularAgua = () => {
        let cloroTotal = litrosCloroDeseado * porcentajeCloroDeseado / 100
        let cloroNecesario5 = cloroTotal / (porcentajeCloroDispuesto / 100)
        setLitrosCloro5(cloroNecesario5)
        let aguaNecesaria = litrosCloroDeseado - cloroNecesario5
        setAgua(aguaNecesaria)
    } 

    useEffect(() => {
        calcularAgua()
    })
    
    const cambiarEstadoLitrosCloroDeseado = (e) => {
        setLitrosCloroDeseado(e.target.value)
    }
    
    const cambiarEstadoPorcentajeCloroDeseado = (e) => {
        setPorcentajeCloroDeseado(e.target.value)
    }

    const cambiarEstadoPorcentajeCloroDispuesto = (e) => {
        setPorcentajeCloroDispuesto(e.target.value)
    }

    return (
        <Layout>
            <Container maxWidth="xs">
                    <h2>Introduzca los valores de Cloro deseados</h2>
                    <FormControl fullWidth className={classes.margin} variant="outlined">
                        <InputLabel htmlFor="cloroml">Cloro en ml</InputLabel>
                        <OutlinedInput
                            id="cloroml"
                            type="number"
                            value={litrosCloroDeseado}
                            onChange={cambiarEstadoLitrosCloroDeseado}
                            endAdornment={<InputAdornment position="start">mL</InputAdornment>}
                            labelWidth={80}
                        />
                        <FormHelperText id="cloroml">1000 mL = 1 L</FormHelperText>
                    </FormControl>
                    <br/> <br/>
                    <FormControl fullWidth className={classes.margin} variant="outlined">
                        <InputLabel htmlFor="porcentajeClorodeseado">Concentracion de Cloro</InputLabel>
                        <OutlinedInput
                            id="porcentajeClorodeseado"
                            type="number"
                            value={porcentajeCloroDeseado}
                            onChange={cambiarEstadoPorcentajeCloroDeseado}
                            startAdornment={<InputAdornment position="start">%</InputAdornment>}
                            labelWidth={170}
                        />
                        <FormHelperText id="cloroml">0.1% es el valor por defecto</FormHelperText>
                    </FormControl>

                    <hr/>
                    <FormControl>                        
                        <div>
                            <h3>Ud Necesita mezclar: </h3>
                            {/* <b>{litrosCloro5}</b>     ml de cloro 
                            al: {porcentajeCloroDispuesto} %
                            <br/>
                            con: <b>{agua}</b> ml de agua 
                            <br/> para obtener lo deseado */}
                        </div>
                    </FormControl>
                    <TableContainer component={Paper}>
                        <Table className={classes.table} aria-label="caption table">
                            <caption>5% es la concentracin por defecto</caption>
                            <TableHead>
                            <TableRow>
                                <TableCell><b>Mezclar</b></TableCell>
                                <TableCell align="right">mL</TableCell>
                                <TableCell align="right">%</TableCell>
                            </TableRow>
                            </TableHead>
                            <TableBody>
                                <TableRow key="agua">
                                    <TableCell component="th" scope="row">
                                        Agua
                                    </TableCell>
                                    <TableCell align="right">{agua} ml</TableCell>
                                    <TableCell align="right">-</TableCell>
                                </TableRow>
                                <TableRow key="cloro">
                                    <TableCell component="th" scope="row">
                                        Cloro
                                    </TableCell>
                                    <TableCell align="right">{litrosCloro5} ml</TableCell>
                                    <TableCell align="right">
                                    <Input 
                                        style={{
                                            maxWidth: "50px"
                                        }}
                                        type="number" 
                                        value={porcentajeCloroDispuesto} 
                                        onChange={cambiarEstadoPorcentajeCloroDispuesto} 
                                        endAdornment={<InputAdornment position="start">%</InputAdornment>}
                                    /> 
                                    </TableCell>
                                </TableRow>
                            </TableBody>
                        </Table>
                    </TableContainer>
                {/* </Paper> */}
            </Container>
        </Layout>
    )
}